.proven-p {
  padding-top: 1.5rem;
  width: 38%;
  margin: auto;
}
.proven-img {
  margin-top: 2rem;
}
.proven-img img {
  width: 75%;
  margin-top: 1rem;
}
.p-bg {
  top: -56px;
  width: 550px;
  transform: translateX(-247px);
}
.pro .fe-title {
  padding: 1rem 0;
 
}
.pro {
  overflow: hidden;
  padding-top: 10px;
}
/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .p-bg {
    top: -19px;
    width: 300px;
    transform: translateX(-160px);
  }
  .pro .fe-title {
    margin-top: 1rem;
  }
  .proven-p {
    padding-top: 1.5rem;
    width: 83%;
    margin: auto;
  }
  .proven-img img {
    margin-top: 0;
    width: 151%;
    margin-right: -264px;
}
}

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
  .p-bg {
    top: -39px;
    width: 438px;
    transform: translateX(-223px);
  }
  .proven-p  {
    font-size: 13px !important;
    line-height: 2;
    width: 65%;
  }
}

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
  .p-bg {
    top: -40px;
    width: 450px;
    transform: translateX(-225px);
  }
  .proven-p  {
    font-size: 13px !important;
    line-height: 2;
    width: 65%;
  }
}

/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .p-bg {
    top: -44px;
    width: 450px;
    transform: translateX(-223px);
  }
}

