.features {
  margin-top: 3rem;
  overflow: hidden;
}
.fe-title {
  padding: 3rem 0;
  z-index: 5;
}
.fe-title h4 {
  font-style: italic;
}
.fe-bg {
  position: absolute;
  top: -7px;
  width: 34%;
  transform: translateX(-211px);
}
h4 {
  font-size: 38px !important;
  font-weight: bold !important;
}
.fe-slider {
  margin-top: -10rem;
  margin-left: 5rem;
  background-image: url("../../assets/images/background-feature.png");
  background-repeat: no-repeat;
  background-size: 100% 90%;
  padding-bottom: 8rem;
  overflow: hidden;
  width: 95%;
  position: relative;
}
.img-badge {
  position: absolute;
  bottom: 17.2%;
  width: 22%;
  left: 115px;
}
/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  h4 {
    font-size: 28px !important;
    font-weight: bold !important;
  }
  .fe-bg {
    top: 23px;
    width: 76%;
    transform: translateX(-46%);
  }
  .img-badge {
    position: absolute;
    bottom: 15.2%;
    width: 78%;
    left: 0;
    transform: translateX(16%);
  }
  .fe-slider {
    margin-top: 0rem;
    margin-left: 0rem;
    background-image: url("../../assets/images/mobile-Mask.png");
    background-repeat: no-repeat;
    background-size: 100% 90%;
    padding-bottom: 5rem;
    overflow: hidden;
    width: 100%;
    position: relative;
  }
  .f-slider {
    padding: 20px;
    margin-top: 3rem;
    transform: translateX(9px);
  }
}

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
  .img-badge {
    bottom: 17.2%;
    width: 44%;
    left: 115px;
  }
  .fe-bg {
    top: 17px;
    width: 55%;
    transform: translateX(-170px);
  }
}

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
  .img-badge {
    bottom: 16.2%;
    width: 46%;
    left: 115px;
  }
  .fe-bg {
    top: 5px;
    width: 48%;
    transform: translateX(-211px);
  }
}

/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .img-badge {
    position: absolute;
    bottom: 17.2%;
    width: 30%;
    left: 115px;
  }
  .card {
    background-color: #251015 !important;
    margin: 20px !important;
    padding-top: 1rem;
    padding-right: 0rem;
    min-height: 362px !important;
  }
  .fe-bg {
    top: 1px;
    width: 40%;
    transform: translateX(-208px);
  }
}

/* // Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
}
