.renew .dash-right {
  background-image: url("../../assets/images/background-renew.png");
}
.renew .versa {
  width: 100%;
  margin-right: 0;
  padding-right: 0;
  margin-left: 0;
}
.renew .easy-dash {
  align-items: flex-end;
  padding-bottom: 4rem;
}
.card-pay {
  margin-top: -5rem;
  width: 100%;
}
.bg-pad {
  height: 600px;
  margin-top: -4rem;
}

/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .renew .dash-right {
    background: none;
  }
  .bg-pad {
    height: auto;
    margin-top: 3rem;
  }
  .card-pay {
    margin-top: -5rem;
    width: 90%;
  }
  .ops {
    display: none !important;
  }
  .renew .easy-dash {
    align-items: flex-end;
    padding-bottom: 1rem;
    padding-right: 0;
    text-align: left;
    padding-left: 1rem;
  }
  
}

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
  .bg-pad {
    height: auto;
    margin-top: 3rem;
  }
  .renew .dash-right {
    background: none;
  }
  .easy-dash h4 {
    text-align: center;
  }
}

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
  .bg-pad {
    height: 420px;
  }
}

/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .bg-pad {
    height: 474px;
  }
}

/* // Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
}
