.faq {
  background-image: url("../../assets/images/FQA-background.png");

  margin-top: -13rem;
  padding-bottom: 2rem;
  position: relative;
  z-index: 2;
  overflow: hidden;
}
.faq .fe-title {
  padding: 20rem 0 3rem;
}
.fo-cl {
  background-color: #30161d;
}
.faq .fe-bg {
  position: absolute;
  top: 265px;
  width: 600px;
  transform: translateX(-300px);
}
.faq .proven-p {
  margin-bottom: 3rem;
}

/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .faq {
    background-image: url("../../assets/images/FQA-background-mobile.png");
    background-size: 100% 120%;
    padding-bottom: 0;
  }
}

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
}

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
}

/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .question .card .card-title {
    font-size: 20px;
  }
  .question .card .card-text {
    font-size: 18px !important;
    margin-top: 3rem;
  }
  .question .slick-next {
    left: 55.59% !important;
  }
  .question .slick-prev {
    left: 41.5%;
  }
  .question .slick-next,
  .question .slick-prev {
    top: 350px;
  }
}

/* // Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .faq {
    background-size: cover;
  }
}
