.header {
  background-image: url("../../assets/images/main-bg.png");
  background-size: cover;
  padding-bottom: 3rem;
}
.logo {
  margin-top: 5rem;
}
.logo img {
  width: 95px;
  position: relative;
  z-index: 1;
}
.h-info {
  margin-top: 2rem;
}
.turbo-bg {
  position: absolute;
  top: -16rem;
  left: 15%;
  width: 63%;
  z-index: 0;
}
.saver {
  padding-top: 4rem;
}
.h-btn {
  background-image: url("../../assets/images/btn-background.png");
  background-repeat: no-repeat;
  border: 0 !important;
  padding: 13px 51px !important;
  font-size: 13px !important;
  font-weight: bold !important;
  background-size: cover;
  margin-top: 2rem;
  position: relative;
  border: none !important;
  border-radius: 0 !important;
}
.h-btn:hover {
  background-image: url("../../assets/images/nav-bg-btn.png");
}
.h-info h3 {
  font-size: 38px !important;
  letter-spacing: 0.28px;
  font-style: italic;
  position: relative;
}
.h1-title {
  font-size: 65px;
  letter-spacing: 0.28px;
  font-style: italic;
  position: relative;
}
.modal-content {
  background-image: url("../../assets/images/contact-background.png");
  background-size: cover;
}
.contact-c {
  background-color: #361b22 !important;
}
.contact-c .fe-bg {
  position: absolute;
  top: 7px;
  width: 40%;
}
@media (min-width: 576px) {
  .modal-dialog {
    max-width: 70% !important;
  }
  .my-form {
    padding: 15px 5%;
  }
  .my-form .h-btn {
    padding: 11px 64px !important;
  }
}
.modal-content {
  border-radius: 18px !important;
}
.my-form {
  padding: 15px 15%;
}
input.form-control {
  height: calc(2em + 0.75rem + 2px);
}
.my-form input::placeholder,
.my-form textarea::placeholder {
  color: rgba(255, 255, 255, 0.356);
}
.contact-c h4 {
  font-style: normal !important;
}
.my-form .h-btn {
  padding: 13px 65px !important;
}
.my-form .form-control,
.my-form .form-control:focus {
  color: #fff;
}

/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .my-form .h-btn {
    padding: 12px 67px !important;
  }
  .header {
    background-image: url("../../assets/images/main-small-bg.png");
    padding-bottom: 0;
  }
  .h-info h3 {
    display: none;
  }
  .logo img {
    width: 65px;
  }
  .logo {
    margin-top: 0rem;
    margin-bottom: 5rem;
  }
  .turbo-bg {
    position: absolute;
    top: -7rem;
    left: -4%;
    width: 100%;
    z-index: 0;
  }
  .h1-title {
    font-size: 28px;
    z-index: 2;
    position: relative;

    padding: 15px;
    padding-top: 0;
    margin-bottom: 2rem;
  }
  .h-btn {
    position: relative;
    padding: 13px 53px !important;
    font-size: 17px !important;
    margin-top: 2rem;
  }
}

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
  .header {
    padding-bottom: 0;
  }
  .turbo-bg {
    top: -11rem;
    left: 15%;
    width: 69%;
  }
  .logo {
    margin-top: 3rem;
  }
  .h-info {
    margin-top: 2rem;
    padding: 0 2rem;
  }
  .h-info h3 {
    font-size: 25px !important;
  }
  .h1-title {
    font-size: 43px;
  }
}

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
  .turbo-bg {
    top: -13rem;
    left: 15%;
    width: 70%;
  }
  .logo {
    margin-top: 4rem;
  }
  .h-info {
    margin-top: 2rem;
    padding: 0 2rem;
  }
  .h-info h3 {
    font-size: 30px !important;
  }
  .h1-title {
    font-size: 52px;
  }
}
@media (max-width: 991.98px) {
}
/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .turbo-bg {
    top: -16rem;
    left: 9%;
    width: 75%;
  }
}

/* // Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
}
