.footer {
  background-color: #200f15;
  padding-top: 1.5rem;
  padding-bottom: 1.2rem;
  font-size: 13px;
  color: rgba(255, 255, 255, 0.726);
  overflow: hidden;
}
.footer a {
  color: rgba(255, 255, 255, 0.726);
}
.footer a:hover {
  color: rgb(255, 255, 255);
}
ul {
  margin-bottom: 0 !important;
  padding: 0 5% !important;
}
.f-twitter {
  font-size: 17px;
  width: 28px;
  height: 28px;
  text-align: center;
  background-color: tomato;
  display: inline-block;
  border-radius: 3px;
  margin-right: 5px;
}
.copy2 {
  margin-top: -10px;
}
.f-twitter i {
  display: inline-block;
  margin-top: 5px;
}
/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .footer {
    padding: 1rem 0;
  }
  ul.d-flex {
    display: block !important;
  }
  .ftr-link {
    display: block;
    margin-bottom: 10px;
  }
  .copy,
  .copy2 {
    display: inline-block;
  }
  .copy2 {
    float: right;
    width: 20%;
    text-align: right;
    margin-top: -0px;
  }
  .copy {
    float: left;
    width: 80%;
  }
  .emailAd {
    display: none;
  }
}

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
  .footer {
    padding: 1rem 0;
  }

  ul.d-flex {
    display: block !important;
  }
  .ftr-link {
    display: block;
    margin-bottom: 10px;
  }
  .copy,
  .copy2 {
    display: inline-block;
  }
  .copy2 {
    float: right;
    width: 30%;
    margin-top: -10px;
    text-align: right;
    margin-top: 0;
  }
  .copy {
    float: left;
    width: 70%;
  }
  .emailAd {
    display: none;
  }
}
