.dash-right {
  background-image: url("../../assets/images/background-access.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  height: 500px;
}

.easy-dash {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90%;
  text-align: left;
  padding: 6rem;
}
.img-dash {
  width: 536px;
  margin-top: -55px;
  margin-top: -55px;
}
.main-dash {
  margin-top: 5rem;
  overflow: hidden;
}
.fe-dash {
  position: absolute;
  top: -15px;
  width: 600px;
  transform: translateX(-300px);
}
.main-dash .fe-title {
  padding-bottom: 0 !important;
}
.down-div {
  margin-bottom: 6rem;
}
.bots  .easy-dash {
  padding-right: 0;
}
/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .fe-dash {
    position: absolute;
    top: -7px;
    width: 500px;
    transform: translateX(-238px);
  }
  .main-dash {
    margin-top: 3rem;
  }
  .easy-dash {
    height: 100%;
    padding: 0 5rem 0 2rem;
  }
  .img-dash {
    width: 77%;
    margin-top: 3rem;
  }
  .dash-right {
    background: none;
    height: auto;
  }
  .easy-dash h4 {
    font-size: 24px !important;
  }
  .bots {
    order: 1;
    margin-top: 2rem;
  }
  .no-bots {
    order: 2;
  }
  .no-bots .img-dash {
    margin-top: 0;
  }
  .bots  .easy-dash {
    padding-right: 20px;
  }
}

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) and (max-width: 767.98px) {
  .fe-dash {
    position: absolute;
    top: -7px;
    width: 500px;
    transform: translateX(-238px);
  }
  .main-dash {
    margin-top: 3rem;
  }
  .easy-dash {
    height: 100%;
    padding: 0 5rem 0 2rem;
  }
  .img-dash {
    width: 77%;
    margin-top: 3rem;
  }
  .dash-right {
    background: none;
    height: auto;
  }
  .easy-dash h4 {
    font-size: 24px !important;
  }
  .bots {
    order: 1;
    margin-top: 2rem;
  }
  .no-bots {
    order: 2;
  }
  .no-bots .img-dash {
    margin-top: 0;
  }
  .bots  .easy-dash {
    padding-right: 20px;
  }
}

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 991.98px) {
  .easy-dash {
    padding: 1rem;
    padding-left: 2rem;
  }
  .dash-right {
    height: auto;
  }
  .img-dash {
    width: 372px;
    margin-top: -55px;
  }
  .bots .easy-dash {
    padding-left: 5rem;
  }
  .easy-dash h4 {
    font-size: 24px !important;
    text-align: center;
  }
}

/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) and (max-width: 1199.98px) {
  .easy-dash {
    padding: 1rem;
    padding-left: 2rem;
  }
  .bots .easy-dash {
    padding-left: 5rem;
  }
  .easy-dash h4 {
    font-size: 30px !important;
    text-align: center;
    padding-left: 3rem;
  }
}

