.navbar-light .navbar-nav .nav-link {
  color: #fff !important;
}
.navbar-expand-lg .navbar-nav .nav-link {
  margin-right: 1.5rem !important;
  margin-left: 1.5rem !important;
}
.navbar {
  padding: 1rem 1rem !important;
}
.navbar-expand-lg .navbar-nav .dash-im {
  background-image: url("../../assets/images/nav-bg-btn.png");
  background-repeat: no-repeat;
  background-size: contain;
  padding-top: 6px;
  padding-right: 1rem !important;
  padding-left: 1rem !important;
  margin-top: 2px;
}
.navbar-expand-lg .navbar-nav .dash-im:hover {
  background-image: url("../../assets/images/btn-background.png");
}
.navbar-nav {
  padding: 10px 0;
}
.navbar-toggler {
  margin-left: auto;
  background-color: #2c141b !important;
  padding: 1rem !important;
  border-radius: 0 0 0 35px !important;
}
.navbar-light .navbar-nav .nav-link {
  position: relative;
  z-index: 10;
}
@media (max-width: 991.98px) {
  .navbar-toggler {
    width: 80px;
    height: 67px;
  }
  .navbar {
    padding: 0 !important;
    
  }
  .nav-color {
    background-color: #2c141b !important;
  }
  .navbar-nav {
    padding-bottom: 1rem !important;
  }
}
@media (max-width: 575.98px) {
  .navbar-toggler {
    width: 67px;
    height: 61px;
  }
  .navbar-toggler {
    padding: 1rem !important;
    border-radius: 0 0 0 30px !important;
  }
}

.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 5000;
  top: 0;
  right: 0;
  background-color: #29131a;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

.sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 25px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

.sidenav a:hover {
  color: #f1f1f1;
}

.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
  z-index: 10000;
  cursor: pointer;
}

@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px;
  }
  .sidenav a {
    font-size: 18px;
  }
}
@media (min-width: 992px) {
  #mySidenav {
    display: none;
  }
}
@media (max-width: 991.98px) {
  #mySidenav .nav-link {
    font-size: 20px;
    margin: 5px;
  }
}
