.f-slider {
  margin-top: 10rem;
  transform: translateX(97px);
}
.card {
  background-color: #251015 !important;
  margin: 20px !important;
  display: inline-block;
  position: relative;
  padding-top: 3rem;
  padding-right: 2rem;
  min-height: 362px !important;
}

.f-slider .card-title {
  font-weight: bold;
  height: 48px;
}
.f-slider .card img {
  position: absolute;
  bottom: 0;
  right: 0;
}
.f-slider .card-body p {
  font-size: 15px !important;
  margin-top: 0;
  padding-right: 1rem;
  height: 130px;
}
.f-slider .card-body a {
  display: inline-block;
  margin-top: 2rem;
  font-size: 13px;
  color: #219882;
}
.slick-prev:before,
.slick-next:before {
  font-size: 23px !important;
}
.slick-prev {
  left: -89px;
}
.slick-next {
  left: -95px;
  top: 260px;
}
.slick-prev:before {
  font-family: "Font Awesome 5 Free";
  content: "\f053" !important;
  font-weight: 900;
  display: inline-block;
  margin-right: 4px;
}
.slick-next:before {
  font-family: "Font Awesome 5 Free";
  content: "\f054" !important;
  font-weight: 900;
  display: inline-block;
  margin-left: 4px;
  position: relative;
}
.slick-prev,
.slick-next {
  width: 50px;
  height: 50px;
  background-color: #a44e48;
  border-radius: 50px;
  color: #fff;
}
.slick-prev:hover,
.slick-next:hover,
.slick-prev:focus,
.slick-next:focus {
  width: 50px;
  height: 50px;
  background-color: #c9483f;
  border-radius: 50px;
}
@media (max-width: 575.98px) {
  .f-slider .card-body p {
    padding-right: 0 !important;
    line-height: 1.9;
  }
  .f-slider .card {
    padding-top: 1rem !important;
  }
  .f-slider {
    padding: 20px;
    margin-top: 3rem;
    transform: translateX(9px);
  }
  .f-slider .card-body a {
    margin-top: 4rem;
  }
  .slick-prev {
    left: 35%;
  }
  .slick-next {
    left: 55%;
  }
  .slick-prev,
  .slick-next {
    top: 110%;
  }
}
.features .slick-dots {
  display: none !important;
}

@media (min-width: 576px) and (max-width: 767.98px) {
  .f-slider .card {
    padding-top: 2rem !important;
  }
  .f-slider .card-body a {
    margin-top: 4rem;
  }
  .f-slider .card-text {
    padding-right: 0 !important;
    margin-top: 2rem;
  }
  .f-slider.card-body {
    padding-right: 0 !important;
  }
}
/* // Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
  .card {
    padding-top: 2rem;
    padding-right: 1rem;
  }
}
