.question-img {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 36%;
}
.question .card {
  min-height: 275px !important;
  padding-top: 1rem;
  padding-right: 10px;
  padding-left: 10px;
}
.card {
  border-radius: 10px !important;
}
.t-link {
  color: #e97169;
}
.t-link:hover {
  color: #a44e48;
}
.question .card .card-title {
  font-weight: bold;
  height: 68px;
  font-size: 26px;
}
.question .card .card-text {
  color: #a44e48;
  font-style: italic;
  font-weight: bold;
  margin-top: 2rem;
  font-weight: bold;
  font-size: 15px !important;
  height: 165px;
}
.slick-dots li button:before {
  color: #fff;
  font-size: 10px;
  opacity: 0.7;
}
.slick-dots li.slick-active button:before {
  opacity: 0.75;
  color: #ff985c;
}
.question .slick-prev {
  left: 43%;
  bottom: 0px;
}
.question .slick-next,
.question .slick-prev {
  top: 390px;
}
.question .slick-next {
  left: 54.59%;
  bottom: 0px;
}
.question .slick-prev,
.question .slick-next {
  width: 30px;
  height: 30px;
  z-index: 100;
}
.question .slick-prev:before,
.question .slick-next:before {
  font-size: 15px !important;
}
.question .slick-dots li {
  margin: 0 !important;
}
.question .slick-dots {
  bottom: -37px;
}
/* // Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) {
  .question .card {
    width: 94% !important;
    margin: 20px 0 !important;
  }
  .question .card .card-title {
    font-size: 17px;
  }
  .question .card .card-text {
    font-size: 12px !important;
    margin-top: 1rem;
    line-height: 1.2 !important;
  }
  .question .slick-next {
    left: 67% !important;
  }
  .question .slick-prev {
    left: 25%;
  }
  .question .slick-next,
  .question .slick-prev {
    top: 363px;
  }
  .question .slick-dots {
    bottom: -28px;
  }
  .question .card-body {
    padding-right: 15px;
    padding-left: 10px;
  }
}

