@media (min-width: 576px) and (max-width: 767.98px) {
  .question .card .card-title {
    font-size: 17px;
  }
  .question .card .card-text {
    font-size: 12px !important;
    margin-top: 1rem;
    height: 220px;
  }
  .question .slick-next {
    left: 59% !important;
  }
  .question .slick-prev {
